import React from 'react';




class Img extends React.Component{

    
    render(){
        
        const { img, alt, onMouseOver,onMouseOut, id}= this.props
        return(
        
            <div>   
                    <img id={id} src={img} onMouseOver={onMouseOver} onMouseOut={onMouseOut} alt={alt}></img>
                   
            </div>
            
            
        );

        
    }
        
  



}


export default Img

