//FUNCION DESPLAZAMIENTO PERSONAJE DE ANIME YUJI ITADORI
function DesplazamientoPersonajeAnimeYuji(scrolled,scrolledMin,scrolledMax){

    var YujiDesplazamiento = document.getElementById('imagenAparicion')

            YujiDesplazamiento.style.width='0%'
            YujiDesplazamiento.style.marginLeft='10%'

            
        if(scrolled > scrolledMin && scrolled < scrolledMax){
            YujiDesplazamiento.style.width='80%'
            
        }else{
            
            YujiDesplazamiento.style.width='0%'
            YujiDesplazamiento.style.marginLeft='60%'
        }
         

}
//FUNCION DESPLAZAMIENTO PERSONAJE ANIME KAGI   
function DesplazamientoPersonajeAnimeKagi(scrolled,scrolledMin,scrolledMax){

    var KagiDesplazamiento = document.getElementById('imagenAparicionSection4')

            KagiDesplazamiento.style.width='0%'
            KagiDesplazamiento.style.marginLeft='10%'

            
        if(scrolled > scrolledMin && scrolled < scrolledMax){
            KagiDesplazamiento.style.width='80%'
            
        }else{
            
            KagiDesplazamiento.style.width='0%'
            KagiDesplazamiento.style.marginLeft='-30%'
        }
         

}
function ModificarContenedorAnimacion(ContenedorAnimado, width, height, margin){

            ContenedorAnimado.style.width= width
            ContenedorAnimado.style.height= height
            ContenedorAnimado.style.margin= margin
}
function ModifiarPersonajeAnime(PersonajeAnime, opacity, width, marginLeft, marginTop, height){
            PersonajeAnime.style.opacity=opacity
            PersonajeAnime.style.width= width
            PersonajeAnime.style.height=height
            PersonajeAnime.style.marginLeft=marginLeft
            PersonajeAnime.style.marginTop= marginTop
}
function ModificarImagenAnimada(imagenAnimada, filter, width, height, margin){
            imagenAnimada.style.filter= filter
            imagenAnimada.style.width= width
            imagenAnimada.style.height= height
            imagenAnimada.style.margin= margin
}

